@import 'styles-base.css';
@import 'styles-nav.css';
@import 'styles-footer.css';

h1, h2, h3, h4, h5, h6 {
    font-family: var(--heading-font);
    font-weight: var(--heading-font-weight);
}

body {
    font-family: var(--paragraph-font);
    font-weight: var(--paragraph-font-weight);
}

.disable-link {
    pointer-events: none;
    cursor: not-allowed;
    display: block;
}
